.about-page {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh; /* Fallback for browsers that do not support Custom Properties */
    height: calc(var(--vh, 1vh) * 100);

    &__text {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #ffffff;
        padding: 20px;
        font-size: calc(14px + 18*(100vw - 320px)/1600);
        font-family: "roseaLight";
        line-height: 1.4;
        padding: 0px 25px;
        transition: all 300ms ease-in-out;

        @media (max-width: 800px) {
            width: 100%;
            font-size: calc(16px + 18*(100vw - 320px)/1600);
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100vh;
        }
    }
    .container {
        display: block;
        height: 100vh;
        width: 50%;

        @media (max-width: 800px) {
            width: 100%;
            opacity: 0.2;
        }
    }
}