.loader {
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    color: #fff;
    z-index: 10;
    h1 {
        font-family: "rosea";
        color: #fff;
    }
    p {
        position: absolute;
        bottom: 20px;
        right: 20px;
        font-family: "rosea";
        font-size: 18px;
    }
}